<template>
    <div class="dictionary">
        <div class="welcome">
            <Img
                :preview="data.item?.cover?.original"
                :original="data.item?.cover?.original"
                :alt="title"
                :skeleton="true"
            />

            <div class="wrapper">
                <div class="title" v-if="title">{{ title }}</div>
                <div class="buttons">
                    <Button :name="t('player.shuffle')" class="tertiary active size-m" icon="random"/>
                    <Button :name="t('player.play')" class="size-m" icon="play"/>
                </div>
            </div>
        </div>

        <EmptyLabel
            class="empty"
            :title="t(`pages.dictionary.empty.title`)"
            :description="t(`pages.dictionary.empty.description`)"
        />
    </div>
</template>

<script setup>
    import { onMounted, onBeforeUnmount, reactive, computed } from 'vue';
    import api from 'ðŸ”—/api';
    import stores from '@/stores';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import { useI18n } from 'vue-i18n';

    const { t } = useI18n();

    const dict = api.dict();
    const locale = stores.locale();
    const tg = stores.tg();

    const props = defineProps({
        code: { type: String },
        type: { type: Object }
    });

    const data = reactive({
        item: null
    });

    const title = computed(() => {
        return data.item?.title ? data.item?.title[locale.locale] || '' : '';
    });


    document.body.classList.add('with-tg-header');
    onMounted(async () => {
        tg.showBackButton();

        data.item = await dict[props.type.code].get(props.code, props.type.id);
        console.log(data.item)
    });

    onBeforeUnmount(() => {
        document.body.classList.remove('with-tg-header');
        tg.hideBackButton();
    });
</script>


<style lang="scss" scoped>
    .dictionary {
        display: flex;
        flex-direction: column;
        flex: 1;
        .welcome {
            height: 384px;
            width: 100%;
            flex-shrink: 0;
            position: relative;

            &:after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: linear-gradient(to bottom, var(--white-0) 68%, var(--white-500) 100%);
                pointer-events: none;
            }
        }
        .wrapper {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            padding: 16px;
            display: flex;
            flex-direction: column;
            gap: 16px;
            justify-content: end;
            z-index: 1;

            .title {
                font: var(--font-h5);
                color: white; //var(--white-500);
                font-weight: bold;
                text-align: center;
                -webkit-text-stroke: 1px rgb(0, 0, 0, .2);
            }

            .buttons {
                min-height: 38px;
                display: flex;
                flex-direction: row;
                gap: 8px;
                align-items: center;
                justify-content: center;

                button {
                    min-width: 132px;
                }
            }
        }

        .empty {
            margin: auto;
        }
    }
</style>
